body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  margin: 1em;
}

.navigation-bar {
  padding: 1em;
  border: 1px solid;

}

nav {
  display: inline-flex;
}

nav div {
  text-align: center;
  border: 1px solid;
  padding: 1em;
  margin-right: 1em;
}

.file-input {
  padding: 0.3em;
  border: 1px solid;
  float: right;
}

.row {
  display: flex;
}

.sidebar {
  width: 30%;
  margin-top: 1em;
  border: 1px solid;
  display: inline-block;
}

.sidebar .side-item {
  margin: 1em;
  padding: 1em;
  border: 1px solid;
  cursor: pointer;
}

.main-pane {
  margin: 1em 0 0 1em;
  border: 1px solid;
  width: 100%;
}
